.carousel {
  margin: 0 0 80px;
  padding: 0 152px;
  display: flex;
  justify-content: center;

  &__list {
    box-sizing: border-box;
    margin: 0 auto;
    padding: 0;
    display: flex;
    justify-content: space-between;
    list-style: none;
    overflow: hidden;
  }

  &__img {
    object-fit: fill;
    width: 100%;
    object-position: center;
    height: 400px;

    &:hover {
      transform: scale(1.2);
      border: 2px solid transparent;
    }
  }

  &__button {
    height: 400px;
    border: 1px solid rgba(180, 189, 195, 1);
  }
}

